body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.loader {
  width: 100bh;
  height: 120%;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Arial;
  padding: 10px;
  background: white; 
}

.header {
  padding: 30px;
  text-align: center;
  background: white;
}

.header h1 {
  font-size: 50px;
  font-family: Tahoma, Verdana, Segoe, sans-serif;
  text-shadow: 2px 2px 6px grey;
}
.header h1:hover{
  -webkit-transform: scale(2);
  transform: scale(2);
}

  .column h2 {
    text-shadow: 2px 2px 8px grey;
  }


.topnav {
  overflow: hidden;
  background-color: #333;
}

/* Style the topnav links */
.topnav a {
  float: centre;
  display: inline-block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  -webkit-text-decoration: indigo;
          text-decoration: indigo;
}

/* Change color on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Create three equal columns that floats next to each other */
.column {
    float: left;
    width: 33.3%;
    padding: 40px;
    border-style: none ridge none ridge;
}

.column p {
  padding: 5px;
  line-height: 200%;
}
img { 
  width:100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 1.5);
}

.row:after {
    content: "";
    display: table;
    clear: both;
}
.footer {
  background-color: rgba(241, 241, 241, 0.308);
  text-align: center;
  height: 150;
  padding color: #333;
  display: flex;
  border-style: double none none none;
  overflow: hidden;
}
.bot{
  flex:1 1;
}
.bot:hover{
  background-color: #333;
  color: white
}


@media screen and (max-width:1000px) {
    .column {
        width: 100%;
    }
}

/* Responsive layout - when the screen is less than 400px wide, make the navigation links stack on top of each other instead of next to each other */
@media screen and (max-width: 400px) {
  .topnav a {
      float: none;
      width: 100%;
  }
}





